<script>
    import {exerciseRoll} from '../store/store'
    import { getDay } from '../utils/getDay';
    import { randomInt } from '../utils/randomInt';
    import {push} from 'svelte-spa-router'
    import Card from './Card.svelte';
    import Button from './Button.svelte';
    import {general} from '../translations/general'
    import RoundButton from './RoundButton.svelte';

    const handleDaily = () => {
        if (!$exerciseRoll.workouts || $exerciseRoll.workouts.length === 0) {
            push('/')
        }
    }

    const rollReps = (index) => {
        const today = getDay(new Date());

        if ($exerciseRoll.timeStamp === today && !$exerciseRoll.workouts[index].exercise.reps) {
            const reps = randomInt(0, $exerciseRoll.workouts[index].exercise.repMax)
            $exerciseRoll.workouts[index].exercise.reps = reps
        }
    }

    const count = (index, amount) => {
        if (!$exerciseRoll.workouts[index].exercise.count) {
            $exerciseRoll.workouts[index].exercise.count = amount
        } else {
            $exerciseRoll.workouts[index].exercise.count = $exerciseRoll.workouts[index].exercise.count + amount
        }
    }

    handleDaily()
</script>
  
<div class="flex flex-col items-center justify-center bg-gray-200 gap-8 p-6">
    {#if $exerciseRoll.workouts}
        {#each $exerciseRoll.workouts as workout, index }
        <Card done={workout.exercise.count >= workout.exercise.reps}>
            <p slot="header">{general[workout.workout]}</p>
            <p slot="sub-header">{general[workout.exercise.name]}:</p>
            <p slot="number">{workout.exercise.reps ? workout.exercise.reps : 0}</p>

            <div slot="count" class="w-full flex flex-col items-center">
                <p class="bg-accent rounded-full w-10 h-10 flex justify-center items-center mb-2 text-background">{workout.exercise.count ? workout.exercise.count : 0}</p>

                <div class="flex py-2 w-full justify-between gap-4">
         

                    <RoundButton on:click={() => count(index, -1)}>-</RoundButton>

                    <RoundButton on:click={() => count(index, -10)}>--</RoundButton>

                    <RoundButton on:click={() => count(index, 1)}>+</RoundButton>

                    <RoundButton on:click={() => count(index, 10)}>++</RoundButton>
                </div>
            </div>

            <Button slot="button" on:click={() => rollReps(index)} done={workout.exercise.count >= workout.exercise.reps}>Roll Reps</Button>
        </Card>
        {/each}
    {/if}
</div>
  